body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-container{
  width: 100%;
  padding: 20px;
}


.inner-container{
  width: 100%;
  background: #f0f4f5;
  padding: 30px 20px;
  border-radius: 10px;
}

.inner-container h3{
  font-size: 30px;
}

.main-cont-flex{
  width: 70%;
  display: flex;
  margin: 30px 0 0 0;
}

.main-box{
  width: 30%;
  padding: 10px;
  background: #fff;
  border-radius: 5px;
  margin: 0 15px 0 0;
}

.main-box:last-child{
  margin: 0;
}

.main-box h5{
  color: #aeafb1;
  font-size: 15px;
}

.main-icon-flex{
  display: flex;
  align-items: center;
}

.icon{
  color: #a8a9ab;
  font-size: 13px;
  margin: 0 5px 0 0;
}

.main-cont-flex h4{
  font-size: 15px;
}

.main-service{
  width: 25%;
}

.main-service .main-icon-flex{
  justify-content: space-between;
}

.main-service .icon{
  margin: 0;
}

.graph-box-flex{
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 30px 0 0 0;
}

.graph-box{
  width: 24%;
  padding: 10px 10px 40px 10px;
  background: #fff;
  border-radius: 10px;
  text-align: center;
  height: 250px;
}

.graph-box h4{
  font-size: 17px;
  text-align: left;
  color: #909090;
}

.graph-box h2{
  font-size: 40px;
  color: #3c3f58;
}

.graph-box .main-icon-flex{
  justify-content: center;
  color: yellowgreen;
  font-size: 30px;
}

.graph-box p{
  margin: 0 0 0 5px;
}

.graph-box h5{
  color: #c6c6c6;
  font-size: 13px;
  margin: 15px 0 0 0;
}

.card-graph{
  width: 48%;
  background: #fff;
  padding: 10px 10px 30px 20px;
  border-radius: 10px;
}

.main-card-box{
  display: grid;
  place-items: center;
  place-content: center;
  padding: 20% 0 0 0;
}
